.buy {
  margin: 25px 0 51px;
}

.buy__descr {
  font-size: 20px;
  font-weight: 400;
  line-height: 22px;
  max-width: 626px;
  width: 100%;
  margin: 23px 0 22px;
}

.buy__list {
  display: flex;
}

.buy__list-item {
  flex: 0 1 310px;
  border: 1px solid #E31A4D;
  border-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 310px;
}

.buy__list-item:not(:last-child) {
  margin-right: 20px;
}